/* ===============================================
# サブヘッダー(sub header)
=============================================== */
.ph {
  position: fixed;
  z-index: 100;
  width: 100%;
  background-color: #ffffff;
}
.ph-container {
  max-width: 1980px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ph-nav {
  display: flex;
  align-items: center;
}
.ph-logo {
  padding: 17px 0 20px 20px;
}
.ph-logo a {
  display: block;
  max-width: 200px;
}
.ph-links {
  display: flex;
  margin-left: 120px;
}
@media screen and (max-width: 767px) {
  .ph-links {
    display: none;
  }
}
.ph-link {
  color: #000000;
}
.ph-link:nth-of-type(n + 2) {
  margin-left: 2rem;
}
.ph-btn {
  display: block;
  color: #ffffff;
  padding: 17px 37px 17px 69px;
  margin: 13px 20px 13px;
  background-color: #978f10;
  position: relative;
}
@media screen and (max-width: 767px) {
  .ph-btn {
    display: none;
  }
}
.ph-btn:before {
  position: absolute;
  top: 16px;
  left: 40px;
  content: "";
  display: inline-block;
  width: 22px;
  height: 22px;
  vertical-align: middle;
  background-image: url(../../../../images/header-btn-icon.png);
  background-position: 0 0;
  background-size: contain;
}
.ph-toggle {
  display: none;
}
@media screen and (max-width: 767px) {
  .ph-toggle {
    display: block;
    padding: 20px;
    cursor: pointer;
  }
  .ph-toggle .toggle-btn {
    position: relative;
    width: 40px;
    height: 30px;
  }
  .ph-toggle .toggle-btn span {
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 4px;
    transition: all 0.4s;
  }
  .ph-toggle .toggle-btn span:nth-of-type(1) {
    top: 0;
  }
  .ph-toggle .toggle-btn span:nth-of-type(2) {
    top: calc(50% - 1px);
  }
  .ph-toggle .toggle-btn span:nth-of-type(3) {
    bottom: 0;
  }
  .ph-toggle .toggle-btn._active span {
    background-color: #000000;
  }
  .ph-toggle .toggle-btn._active span:nth-of-type(1) {
    width: 50%;
    transform: translate3d(3px, 6px, 0) rotate(45deg);
  }
  .ph-toggle .toggle-btn._active span:nth-of-type(2) {
    transform: translate3d(-1px, 0, 0) rotate(-45deg);
  }
  .ph-toggle .toggle-btn._active span:nth-of-type(3) {
    width: 51%;
    transform: translate3d(17px, -7.5px, 0) rotate(45deg);
  }
  .ph-toggle .toggle-btn span {
    background-color: #000000;
  }
}